import React, { PropTypes } from 'react';
import cn from 'classnames';

const Lefttext = ({
  text,
  fontSize = '1em',
  color = 'rgba(220, 220, 220, 1.0)',
  textBackgroundColor = 'rgba(0,0,150,1.0)',
  backgroundColor = 'rgba(227,205,203, 0.7)',
  wrapperBackgroundColor = 'rgba(20,20,20, 1.0)',
  lineHeight = '170%',
  height = '',
  textAlign = 'left',
  verticalAlign = 'top',
  extraTextStyle = {},
  extraBackgroundStyle = {},
  extraWrapperStyle = {},
  extraTextClass = [],
  extraBackgroundClass = [],
  extraWrapperClass = [],
}) => {
  const wrapperStyle = {
    height,
    backgroundColor: (wrapperBackgroundColor === null) ? backgroundColor : wrapperBackgroundColor,
    ...extraWrapperStyle,
  };
  const backgroundStyle = {
    backgroundColor,
    textAlign: 'left',
    padding: '3px',
    verticalAlign,
    whiteSpace: 'pre-wrap',
    ...extraBackgroundStyle,
  };
  const textStyle = {
    fontSize,
    color,
    maxWidth: '70%',
    padding: '3px 10px',
    marginLeft: '3px',
    display: 'inline-block',
    backgroundColor: textBackgroundColor,
    lineHeight,
    textAlign: 'left',
    verticalAlign,
    borderRadius: '10px',
    whiteSpace: 'pre-wrap',
    ...extraTextStyle,
  };
  const wrapperFlexClass = cn(
    'row',
    'center-xs',
    'center-sm',
    'center-md',
    'center-lg',
    'middle-xs',
    'middle-sm',
    'middle-md',
    'middle-lg',
    ...extraWrapperClass,
  )
  const flexClasses = cn(
    'col-xs-11',
    'col-sm-8',
    'col-md-6',
    'col-lg-4',
    ...extraBackgroundClass,
  );
  const textClasses = cn(
    //'col-xs-22',
    //'col-sm-16',
    //'col-md-12',
    //'col-lg-8',
    ...extraTextClass,
  );
  return (
    <div className={wrapperFlexClass} style={wrapperStyle}>
      <div className={flexClasses} style={backgroundStyle}>
        <div className={textClasses} style={textStyle}>
         {text}
        </div>
      </div>
    </div>
  );
};

Lefttext.propTypes = {
  text: PropTypes.string.isRequired,
  fontSize: PropTypes.string,
  color: PropTypes.string,
  textBackgroundColor: PropTypes.string,
  backgroundColor: PropTypes.string,
  lineHeight: PropTypes.string,
  height: PropTypes.string,
  extraStyle: PropTypes.object,
};

export default Lefttext;



